body{
    background-color: whitesmoke;
    color: black;
}

.flist{
background-color:black;
color: white;
border-radius: 2px;
}

.cover{
    background-size: cover;
    background-position: center;
    width: 300px; height: 337px; object-fit: fill;
}
.add-todo-btn {
    font-size: 13px;
    border: 1px solid transparent;
    text-transform: uppercase;
    background-color: #DEA303;
    color: white;
    border-radius: 3px;
    padding: 4px 10px 4px 10px;
    font-family: "Montserrat", Sans-serif;
    letter-spacing: 1px;
    font-weight: 500;
} 

.add-todo-btn:hover{
    background-color: #000000;
}



.renew-todo-btn{
    font-size: 16px;
    letter-spacing: 1px;
    border: 1px solid transparent;
    text-transform: uppercase;
    background-color: #DEA203;
    color: white;
    border-radius: 4px;
    padding: 5px 16px 5px 16px;
    font-family: "Montserrat", Sans-serif;
    letter-spacing: 1px;
    font-weight: 500;
}

.renew-todo-btn:hover{
    background-color: #000000;
    color: white;
}

.todos{
    font-family: "Montserrat", Sans-serif;
    font-size: 14px;
    color: #000000;
}


.my-margin{
    margin: 0.5rem;
}


.todo-input{
    width: 400px;
}

.my-head{
    text-transform: uppercase;
    font-size: 26px;
}

.my-head-2{
    text-transform: uppercase;
    font-size: 22px;
}
@media screen and (max-width: 500px) {
    .todo-input{
        width: 90%;
    }

    .todos{
        font-size: 14px;
    }

    .my-flex{
        flex-direction: column;
    }

    
.add-todo-btn {
    font-size: 11px;
    padding: 2px 8px 2px 8px;
    } 



.renew-todo-btn{
    font-size: 13px;
    padding: 3px 12px 3px 12px;
}

.my-margin{
    margin: 0.2rem;
}
.flist{
    width: 90%;
}
}